.expense-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 1rem 0;
    padding: 1rem;
    background-color: #373535;
    color: #e7e4e4;
    border-radius: 12px;
    box-shadow: 0 1px 8px rgb(0, 0, 0, 0.25);

}

.expense-item__description {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem 0;
    margin-left: 0.5rem;
    flex: 1;
}

.expense-item__price {
    font-size: 0.95rem;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid #40005d;
    background-color: rgb(64, 0, 93);
    color: white;
    border-radius: 12px;
    margin-left: 1rem;
}