.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

/* form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
} */

.new-expense__control label {
  font-weight: bold;
  margin-bottom: 0.5rem;
  margin-top: 0.5rem;
  display: block;
}

.new-expense__control input {
  font: inherit;
  padding: 0.5rem;
  border-radius: 6px;
  border: 1px solid #ccc;
  width: 20rem;
  max-width: 100%;
}

.new-expense__actions {
  margin-top: 1.5rem;
  text-align: right;
}
